.bg-custom {
    --bs-bg-opacity: 1;
    background-color: rgb(255, 255, 255, 0.2);
    background-blend-mode: multiply,multiply;
}

.navbar-brand {
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    padding: 5px 15px 5px 15px;
    border-radius: 5px;
    font-weight: bold;
}

.nav-link {
    color: #667eea !important;
    text-shadow: #764ba2 0em 0em;
    font-weight: bold;
}

@media (max-width: 991.98px) {
    .offcanvas-collapse {
        position: fixed;
        top: 56px; /* Height of navbar */
        bottom: 0;
        right: 100%;
        width: 100%;
        padding-right: 1rem;
        padding-left: 1rem;
        overflow-y: auto;
        visibility: hidden;
        background-color: rgb(255, 255, 255, 0.2);
        transition: transform 0.3s ease-in-out, visibility 0.3s ease-in-out;
    }

        .offcanvas-collapse.show {
            visibility: visible;
            width: 100%;
            transform: translateX(100%);
        }
}

.nav-scroller {
    position: relative;
    z-index: 2;
    height: 2.75rem;
    overflow-y: hidden;
}

    .nav-scroller .nav {
        display: flex;
        flex-wrap: nowrap;
        padding-bottom: 1rem;
        margin-top: -1px;
        overflow-x: auto;
        color: rgba(255, 255, 255, 0.75);
        text-align: center;
        white-space: nowrap;
        -webkit-overflow-scrolling: touch;
    }

.nav-underline .nav-link {
    padding-top: 0.75rem;
    padding-bottom: 0.75rem;
    font-size: 0.875rem;
    color: #6c757d;
}

    .nav-underline .nav-link:hover {
        color: #007bff;
    }

.nav-underline .active {
    font-weight: 500;
    color: #343a40;
}

.text-white-50 {
    color: rgba(255, 255, 255, 0.5);
}

.bg-purple {
    background-color: #6f42c1;
}

.bd-placeholder-img {
    font-size: 1.125rem;
    text-anchor: middle;
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none;
}

@media (min-width: 768px) {
    .bd-placeholder-img-lg {
        font-size: 3.5rem;
    }
}
